document.addEventListener('DOMContentLoaded', function(){ //Documento este listo - completar la pagina, sin cargar estilos
    crearGaleria();
});

function crearGaleria(){
    const galeria =document.querySelector('.galeria-imagenes'); // seleccionamos la clase.galeria-imagen
    for(let i= 1; i <= 12; i++){
        const imagen = document.createElement('IMG');
        imagen.src =`build/img/thumb/${i}.webp`;
        imagen.dataset.imagenId =  i; //me dice a que imagen me refiero, me brinda datos para individualizarla- le agrega el atributo de id y un n° para cada uno.
        
        //Añadir la funcion mostrar imagen
        imagen.onclick = mostrarImagen// se ve en la consola

        const lista= document.createElement('LI');
        lista.appendChild(imagen);
        galeria.appendChild(lista);
    }
};

function mostrarImagen(e){

    const id = parseInt(e.target.dataset.imagenId) //convertir el string que tenga estructura de n° a un n°

    //Generar la imagen grande
    const imagen =document.createElement('IMG');
    imagen.src =`build/img/grande/${id}.webp`;

    //Overlay
    const overlay =document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay'); // creamos una clase en el html llamada overlay
    

    //Crear Botón para cerrar imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent ='X';
    cerrarImagen.classList.add('btn-cerrar');
    

    //Cerrar imagen fuera del boton
    overlay.onclick =function(){
        overlay.remove();
        body.classList.remove('fijar-body');
    }
    //Configurar Botón para cerrarse la imagen
    cerrarImagen.onclick = function(){
      overlay.remove();  
      body.classList.remove('fijar-body'); //remuevo el fijar body para que vuelva a hacer scroll
    }

    overlay.appendChild (cerrarImagen);
    //Mostrar en HTML
    const body =document.querySelector('body');
    body.appendChild(overlay);     //appendChild- agrega codigo dentro de html
    body.classList.add('fijar-body'); // crear clase para le body
}

