document.addEventListener('DOMContentLoaded', function() {
    scrollNav();
    
    navegacionFija();

});

function navegacionFija(){
    
    const barra =document.querySelector('.header');

    //Registrar el Intersection Observer // con cada scroll, revisa si el menu esta visible en el navegador o no esta visible 
    const observer = new IntersectionObserver( function(entries){
        if (entries[0]. isIntersecting){
            barra.classList.remove('fijo');
        }
        else{
            barra.classList.add('fijo');
        };
    });

    //Elemento a observar
    observer.observe(document.querySelector('.sobre-festival')); //el elemento a observar lo atamos a un lugar de la pagina
};


function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal a');

    enlaces.forEach( function( enlaces ) { //dividimos los enlaces de a 1
        enlaces.addEventListener('click', function(e) {
            e.preventDefault();
            const seccion = document.querySelector (e.target.attributes.href.value);

            seccion.scrollIntoView({
                behavior: 'smooth'
            });

        });
    });
    
};
//enlaces.addEventListener('click', function(){ });  //no se puede seleccionar multiples elementos con un addEventListener (da error), solo se puede de a uno
